.main-header-menu-toggle {
	display: none;
	position: relative;
	z-index: 30;
	width: 24px;
	height: 24px;
	background-color: inherit;
	@media screen and (max-width: 1000px) {
		display: inline-block;
	}
	//   ,  
	&__item {
		position: absolute;
		background: #fff;
		width: 100%;
		height: 2px;
		left: 0;
		top: 9px;
		transition: all 0.3s ease 0s;
		opacity: 0.6;
	}
	//   
	&::before,
	&::after {
		content: "";
		position: absolute;
		background: #fff;
		width: 100%;
		height: 2px;
		left: 0;
		transition: all 0.3s ease 0s;
		opacity: 0.6;
	}
	// ,  
	&::before {
		top: 0;
	}
	// ,  
	&::after {
		bottom: 0;
	}
	//   45   
	&.active::before {
		transform: rotate(45deg);
		top: 9px;
	}
	//   45   
	&.active::after {
		transform: rotate(-45deg);
		bottom: 9px;
	}
	//     
	&.active span {
		transform: scale(0);
	}
}